import React, { FC } from 'react';
import styled from 'styled-components';
import heroSectionBackground from '../../images/Backgrounds/glass-lines-background.webp';
import { colors, opacityToHex } from '../../utils/colors';
import { device } from '../../utils/layoutBreakpoints';
import { InternalLinks } from '../../utils/links';
import { Text } from '../common';
import { PrimaryButton } from '../common/Buttons/Buttons';
import { Container } from '../common/Container/Container';
import Clients from './Clients';
import { StaticImage } from 'gatsby-plugin-image';
import { Link } from 'gatsby';

const StyledHeroSection = styled.section`
  background-image: url(${heroSectionBackground});
  background-color: ${colors.secondary.gray700 + opacityToHex['0.05']};
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 40px;
  overflow: hidden;
  @media ${device.laptop} {
    padding-bottom: 80px;
    padding-top: 64px;
  }
`;

const StyledHeroInfo = styled.div`
  padding-top: 40px;
  text-align: center;
  font-family: 'Rubik';
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;

  h1 {
    font-weight: 600;
    letter-spacing: -1px;
  }

  @media ${device.laptop} {
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
    padding-bottom: 0;
  }

  @media ${device.desktop} {
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
  }

  p {
    line-height: 1.2rem;
    font-weight: 500;
  }
`;

const HeroSamples = styled.div`
  min-height: 400px;
  position: relative;
  margin-top: 30px;
  display: none;

  @media ${device.mobileM} {
    margin-left: 4vw;
  }

  @media ${device.mobileL} {
    margin-left: 22vw;
  }

  @media ${device.tablet} {
    margin-left: 35vw;
    min-height: 550px;
  }

  @media ${device.laptop} {
    margin-left: -25px;
    width: 50%;
    justify-content: flex-end;
    display: flex;
  }

  & > div {
    position: absolute;
    transform: scale(0.7);
    @media ${device.tablet} {
      transform: scale(1);
    }
    @media ${device.laptop} {
      transform: scale(0.9);
    }
  }
  & > div:first-child {
    top: -20px;
    left: -50px;
    z-index: 2;
    @media ${device.laptop} {
      left: -30px;
    }
  }
  & > div:nth-child(2) {
    left: -180px;
    @media ${device.tablet} {
      top: 30px;
    }
    @media ${device.laptop} {
      left: 0;
    }
  }
  & > div:nth-child(3) {
    top: 65px;
    @media ${device.tablet} {
      top: 130px;
      left: 60px;
    }
    @media ${device.laptop} {
      left: inherit;
    }
  }
`;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 16px;
  align-items: center;
  & > p:first-child {
    font-size: 0.8rem;
    font-weight: 700;
    @media ${device.laptop} {
      font-size: 1.2rem;
    }
  }
  @media ${device.laptop} {
    width: 680px;
    margin-left: 0;
    margin-bottom: 40px;
    gap: 20px;
    align-items: flex-start;
  }
`;

const LinksWrapper = styled.div`
  display: flex;
  // align-items: center;
  // flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 16px;

  @media ${device.laptop} {
    flex-direction: row;
    gap: 32px;
  }
`;

const ApiForMessage = styled.span`
  display: flex;
  align-items: center;
  flex-direction: column;
  .firstPart {
    white-space: nowrap;
  }
  .animatedTextWrapper {
    display: flex;
    align-items: center;
    flex-direction: row;
    .animatedText {
      background-color: ${colors.secondary.highlighter};
      white-space: nowrap;
    }
    .textCursor {
      display: block;
      width: 2px;
      height: 38px;
      background: ${colors.primary.blue};
      margin-left: 1px;
      border-radius: 2px;
      animationname: none;
      animation-duration: 1s;
    }
  }

  @keyframes fadeAnimation {
    0% {
      opacity: 1;
    }
    30% {
      opacity: 0;
    }
    35% {
      opacity: 0;
    }
    65% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }
  &.idling {
    .textCursor {
      animation: fadeAnimation 1s ease-in infinite;
    }
  }
  @media ${device.laptop} {
    align-items: flex-start;
    flex-direction: row;
    .animatedTextWrapper {
      .textCursor {
        width: 3px;
        height: 2.7rem;
        margin-left: 2px;
      }
    }
  }
`;

const HeroParagraph = styled.p`
  max-width: 540px;
`;

const ImageWrapper = styled.div`
  .heroImage {
    position: relative;
    top: -20px;
    display: none;
    overflow: visible;

    img {
      width: 534px;
      height: 401px;
    }

    @media ${device.laptop} {
      display: block;
    }
  }
`;

const HeroSection: FC = () => {
  return (
    <StyledHeroSection>
      <Container>
        <StyledHeroInfo>
          <Wrapper>
            <Text.Title.Bold.xl>
              APIs for the next generation of business text messaging.
            </Text.Title.Bold.xl>
            <HeroParagraph>
              <Text.Regular.Bold.md>
                Enable your software or application with the best SMS APIs for 10DLC
                messaging. Offer your customers 99%+ message deliverability with automated
                A2P compliance and rate limit management. Explore our developer-friendly
                programmable SMS APIs for faster, easier and more reliable A2P messaging.
              </Text.Regular.Bold.md>
            </HeroParagraph>
            <LinksWrapper>
              <div>
                <Link to={InternalLinks.GetDemo}>
                  <PrimaryButton>Get a Demo</PrimaryButton>
                </Link>
              </div>
            </LinksWrapper>
          </Wrapper>
          <ImageWrapper>
            <StaticImage
              src="../../images/Samples/heroImageMessages.png"
              alt="Example of an outbound message"
              placeholder="none"
              className="heroImage"
            ></StaticImage>
          </ImageWrapper>
          {/* <HeroSamples>
            <div>
              <RequestSample />
            </div>
            <div>
              <RequestBodySample />
            </div>
            <div>
              <DinnerMessageSample />
            </div>
          </HeroSamples> */}
        </StyledHeroInfo>
      </Container>
      <Clients />
      {/* <Partners /> */}
    </StyledHeroSection>
  );
};

export default HeroSection;
