import React from 'react';
import { Container } from '../common/Container/Container';
import styled from 'styled-components';
import PlusIcon from '../common/Icons/PlusIcon/PlusIcon';
import { device } from '../../utils/layoutBreakpoints';
import { StaticImage } from 'gatsby-plugin-image';
import { Text } from '../common';
import background from '../../images/Backgrounds/gradient-background.webp';
import CustomLink from '../common/Link';
import { InternalLinks } from '../../utils/links';
import { LinkElementInternal } from '../common/Link/styled';
import { Link } from 'gatsby';

const Collaboration = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  transition: 0.3s;
  &:hover {
    transform: scale(1.05);
    transition: 0.3s;
  }
  & > div:first-child {
    max-width: 80%;
  }
  & > div:last-child {
    max-width: 80%;
  }
  @media ${device.mobileL} {
    & > div:first-child {
      max-width: 70%;
    }
    & > div:last-child {
      max-width: 70%;
    }
  }
  @media ${device.tablet} {
    flex-direction: row;
    & > div:first-child {
      max-width: 50%;
    }
    & > div:last-child {
      max-width: 50%;
    }
  }
`;

const StyledCampaignRegistry = styled.section`
  position: relative;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  gap: 40px;
  background-image: url(${background});
  background-size: cover;
  background-position: 50%;
  border-radius: 24px;
  padding: 30px;
  @media ${device.mobileL} {
    padding: 60px;
  }
  @media ${device.laptop} {
    padding: 100px 200px;
  }
  ${LinkElementInternal} {
    display: flex;
    justify-content: center;
  }
`;

const Links = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
  @media ${device.tablet} {
    flex-direction: row;
    gap: 20px;
  }
`;

const CampaignRegistry = () => {
  return (
    <Container>
      <StyledCampaignRegistry>
        <Link to={InternalLinks.A2P10DlcGuide + '#a2p-10dlc-registration-a-z'}>
          <Collaboration>
            <StaticImage
              src="../../images/Logo/Partners/CampaignRegistryFullLogo.png"
              alt="Campaign Registry"
              placeholder="none"
            ></StaticImage>
            <PlusIcon color="black" />
            <StaticImage
              src="../../images/Logo/Telgorithm/FullLogo.png"
              alt="Telgorithm"
              placeholder="none"
            ></StaticImage>
          </Collaboration>
        </Link>
        <p>
          <Text.Regular.Bold.lg>
            We are partnered directly with The Campaign Registry (TCR) for an API-based
            registration. This means an automated A2P 10DLC registration process saving
            your team hours or manual work and no disruptions to your software or
            application's SMS/MMS during setup.
          </Text.Regular.Bold.lg>
        </p>
        <p>
          <Text.Regular.Bold.lg>
            Need support registering your customer's 10DLC numbers with TCR?
          </Text.Regular.Bold.lg>
        </p>
        <Links>
          <CustomLink address={InternalLinks.ContactUs} arrow>
            Talk to an expert
          </CustomLink>
          <CustomLink address={InternalLinks.A2P10DlcGuide} arrow>
            Explore 10DLC Guide
          </CustomLink>
        </Links>
      </StyledCampaignRegistry>
    </Container>
  );
};

export default CampaignRegistry;
