import React from 'react';
import styled from 'styled-components';
import { Container } from '../common/Container/Container';
import Slider from 'react-slick';
import { StaticImage } from 'gatsby-plugin-image';
import { device } from '../../utils/layoutBreakpoints';
import { Divider } from "../common";

const StyledPartners = styled.section`
  text-align: center;

  .slick-track {
    display: flex;
    align-items: flex-end;
    object-fit: contain;
  }
  .slick-slide {
    margin: 0 10px;
    box-sizing: border-box;
  }

  margin: 40px 0;
`;

const Title = styled.h2`
  width: 100%;
  font-size: 22px;
  font-family: 'Poppins';
  font-weight: 600;
  line-height: 28px;
  text-align: center;

  @media ${device.laptop} {
    text-align: left;
    width: 30%;
    font-size: 32px;
    line-height: 38px;
  }

  @media ${device.desktop} {
    width: 25%;
  }
`;

const ImageWrapper = styled.div`
  display: flex;
  align-items: flex-end;
  img {
    max-height: 50px;
    user-select: none;
    object-fit: contain;
  }
`;

const PartnersContent = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 15px;
  padding-bottom: 40px;
  padding-top: 40px;
  @media ${device.laptop} {
    justify-content: space-between;
    flex-direction: row;
    padding-bottom: 60px;
    padding-top: 60px;
  }
`;

const SliderContainer = styled.div`
  width: 100%;
  @media ${device.laptop} {
    width: 70%;
  }
  @media ${device.desktop} {
    width: 75%;
  }
`;

const Partners = () => {
  const settings = {
    infinite: false,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 0,
    autoplay: false,
    autoplaySpeed: 2000,
    arrows: false
  };

  return (
    <StyledPartners>
      <Container>
        <Divider />
        <PartnersContent>
          <Title>Partnered with:</Title>
          <SliderContainer>
            <Slider {...settings}>
              <ImageWrapper>
                <StaticImage
                  src="../../images/Logo/Partners/CampaignRegistryFullLogo.png"
                  alt="Campaign Registry"
                  placeholder="none"
                  objectFit="contain"
                ></StaticImage>
              </ImageWrapper>
              <ImageWrapper>
                <StaticImage
                  src="../../images/Logo/Partners/NetNumberLogo.png"
                  alt="NetNumber"
                  placeholder="none"
                  objectFit="contain"
                ></StaticImage>
              </ImageWrapper>
            </Slider>
          </SliderContainer>
        </PartnersContent>
        <Divider />
      </Container>
    </StyledPartners>
  );
};

export default Partners;
